<template>
    <div>
        <!--  -->
        <button @click="showRadarChart" class="custom-btn"></button>
        <button @click="tomianshi" class="custom-btn"></button>

        <!--  -->
        <div v-if="showModal" class="modal">
            <div class="modal-content"> <!--  -->
                <button @click="closeModal" class="close-btn"></button>
                <h2></h2>
                <canvas ref="radarChart"></canvas>
                <div class="ability-details">
                    <h3></h3>
                    <ul>
                        <li v-for="(indicator, index) in indicators" :key="index">
                            {{ indicator }}:
                            <span :class="values[index] === 100 ? 'correct' : 'incorrect'">
                                {{ values[index] === 100 ? '' : '' }}
                            </span>
                        </li>
                    </ul>
                </div>
                <div class="ai-evaluation">
                    <h3>AI</h3>
                    <p>{{ aiEvaluation }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Chart, registerables, RadarController, PolarAreaController } from 'chart.js';

export default {
    data() {
        return {
            showModal: false,
            indicators: [], // 5
            values: [],     // 0-100=100=0
            aiEvaluation: '',
            chart: null
        };
    },
    mounted() {
        Chart.register(...registerables);
        Chart.register(RadarController, PolarAreaController);
        window.Chart = Chart;
    },
    methods: {
        tomianshi(){
            this.$router.push('/mianshi'); 
        },
        async showRadarChart() {
            try {
                const question = localStorage.getItem('question');
                const answer = localStorage.getItem('answer');

                if (!question || !answer) {
                    alert('');
                    return;
                }

                //  5AI0-100
                const prompt = `
          ${question}
          ${answer}
          
          
          1. 5CNNRNNPCATransformer
          2. 
             - indicators["1", "2", "3", "4", "5"]
             - values1000
          3. 
             indicators = ["Term1", "Term2", "Term3", "Term4", "Term5"]
             values = [100, 0, 100, 0, 100]
        `;

                const response = await fetch('https://117.72.49.76:443/post_answer', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ user_id: 'test_user', question: prompt })
                });

                const data = await response.json();
                const chatText = data.data || '';

                this.parseRadarData(chatText);
                this.aiEvaluation = await this.generateAIEvaluation();

                this.showModal = true;
                await this.$nextTick(); // DOM
                this.renderRadarChart();
            } catch (error) {
                console.error(':', error);
                alert('');
            }
        },

        parseRadarData(chatText) {
            // 5
            const indicatorsMatch = chatText.match(/indicators\s*=\s*\[([^\]]+)\]/i);
            const valuesMatch = chatText.match(/values\s*=\s*\[([^\]]+)\]/i);

            // 
            this.indicators = (indicatorsMatch?.[1] || '')
                .split(',')
                .map(term => term.trim().replace(/["']/g, ''));

            // 0100
            this.values = (valuesMatch?.[1] || '')
                .split(',')
                .map(val => {
                    const num = parseFloat(val.trim());
                    return num === 1 ? 100 : 0; // 1/0
                });
        },

        renderRadarChart() {
            const ctx = this.$refs.radarChart.getContext('2d');
            if (this.chart) this.chart.destroy();

            this.chart = new Chart(ctx, {
                type: 'radar',
                data: {
                    labels: this.indicators,
                    datasets: [{
                        label: '',
                        data: this.values,
                        borderColor: '#4CAF50',
                        backgroundColor: 'rgba(76, 175, 80, 0.2)',
                        pointRadius: 5
                    }]
                },
                options: {
                    scale: {
                        ticks: {
                            beginAtZero: true,
                            max: 100,      // 0-100
                            stepSize: 20,  // 20
                            callback: (value) => `${value}` // "80"
                        }
                    },
                    legend: { position: 'top' },
                    layout: { padding: 20 }
                }
            });
        },

        async generateAIEvaluation() {
            // 5
            const prompt = `
        AI5100=0=
        ${this.indicators.map((term, i) => `
        - ${term}: ${this.values[i] === 100 ? '' : ''}
        `).join('')}
        
        100-200
      `;

            const response = await fetch('https://117.72.49.76:443/post_answer', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ user_id: 'test_user', question: prompt })
            });
            return (await response.json()).data || '';
        },

        closeModal() {
            this.showModal = false;
            if (this.chart) this.chart.destroy();
        }
    },
    beforeDestroy() {
        if (this.chart) this.chart.destroy();
    }
};
</script>

<style scoped>
/*  */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 12px;
    width: 80%;
    max-width: 600px;
    position: relative;
    max-height: 80vh;
    /*  */
    overflow-y: auto;
    /*  */
}

.close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.2em;
    cursor: pointer;
}

.ability-details {
    margin: 15px 0;
    padding: 15px;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
}

.ai-evaluation {
    margin-top: 20px;
    padding: 15px;
    background: #f8f9fa;
    border-radius: 8px;
}

.correct {
    color: #2ecc71;
}

.incorrect {
    color: #e74c3c;
}
</style>